import React from "react";
import TextHeader from "../../sectionTextHeader";
import { GatsbyImage } from "gatsby-plugin-image";

export function RatingAndCheckpointsSection({ data }) {
  const { title, subtitle, text, visuals } = data;
  const images = visuals[1].images;

  return (
    <div id="rating-checkpoints-app" className="final-images-subsection">
      <div className="section-text">
        <TextHeader title={title} subtitle={subtitle} />
        <p className="article-text">{text}</p>
      </div>
      <div className="images rating-checkpoints">
        <figure id="checkpoint-default">
          <GatsbyImage
            fluid={images[0].img.childImageSharp.gatsbyImageData.fluid}
            image={images[0].img.childImageSharp.gatsbyImageData}
            data-pin-no-hover="true"
            nopin="nopin"
          />
          <figcaption>{images[0].caption}</figcaption>
        </figure>
        <figure id="input-checkpoint">
          <GatsbyImage
            fluid={images[1].img.childImageSharp.gatsbyImageData.fluid}
            image={images[1].img.childImageSharp.gatsbyImageData}
            data-pin-no-hover="true"
            nopin="nopin"
          />
          <figcaption>{images[1].caption}</figcaption>
        </figure>
        <figure id="default-rating">
          <GatsbyImage
            fluid={images[2].img.childImageSharp.gatsbyImageData.fluid}
            image={images[2].img.childImageSharp.gatsbyImageData}
            data-pin-no-hover="true"
            nopin="nopin"
          />
          <figcaption>{images[2].caption}</figcaption>
        </figure>
        <figure id="rating-review">
          <GatsbyImage
            fluid={images[3].img.childImageSharp.gatsbyImageData.fluid}
            image={images[3].img.childImageSharp.gatsbyImageData}
            data-pin-no-hover="true"
            nopin="nopin"
          />
          <figcaption>{images[3].caption}</figcaption>
        </figure>
      </div>
    </div>
  );
}
