import React from "react";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import rightImg from "../../images/page-decoration/right-decoration-leaves.png";
import leftImg from "../../images/page-decoration/left-decoration-leaves.png";
import leftCurly from "../../images/page-decoration/left-curly-decoration.png";
import rightCurly from "../../images/page-decoration/right-curly-decoration.png";

export default function CaseStudyHeader({ data }) {
  const reCoverImg = getImage(
    data.masthead_img?.childImageSharp?.gatsbyImageData
  );

  //src/images/page-decoration/right-decoration-leaves.png
  return (
    <header className="case-study-header">
      <div className="masthead-container row">
        <div className="masthead-case-study-title row">
          <img className="header-decoration" src={leftImg} />
          <h1>{data.title}</h1>
          <img className="header-decoration" src={rightImg} />
        </div>
        <div className="styled-container">
            <img className="curly-timeframe-decoration" src={leftCurly}/>
          <div className="small time-frame-text">{data.time_frame}</div>
          <img className="curly-timeframe-decoration" src={rightCurly}/>
        </div>
        <div className="small-empty-spacer-block"></div>
        <div id="masthead-image-container" className="row">
          <GatsbyImage
            fluid={data.masthead_img.childImageSharp.fluid}
            image={reCoverImg}
            layout="fullWidth"
            data-pin-no-hover="true"
            nopin="nopin"
          />
        </div>
      </div>
    </header>
  );
}
