import React from "react";
import { graphql } from "gatsby";
import { GatsbyImage } from "gatsby-plugin-image"
import CaseStudyLayout from "../../components/case-studies/caseStudyLayout";
import CaseStudySection from "../../components/case-studies/caseStudySection";
import RoleImage from "../../images/page-decoration/Role.png";
import TeamImage from "../../images/page-decoration/Team.png";
import TextHeader from "../../components/case-studies/sectionTextHeader";
import SectionHeader from "../../components/case-studies/sectionHeader";
import {
  MainPagesSection,
  FilteringSystemsSection,
  GameDetailsSection,
  GenresAndThemesSection,
  PersonalListsSection,
  UserProfileSection,
  RatingAndCheckpointsSection,
} from "../../components/case-studies/gameshelf/final-designs-sections";
import "../../styles/caseStudy.scss";

export default function GameShelfPage({ data }) {
  const study = data.allMarkdownRemark.edges[0].node;
  const frontmatter = study.frontmatter;

  const project = frontmatter.gameshelf;
  const overview = {
    role: frontmatter.role,
    team: frontmatter.team,
    project_goal: frontmatter.project_goal,
    project_description: frontmatter.project_description,
    tools: frontmatter.tools,
  };

  const conclusion = frontmatter.conclusion;
  const sections = project.hi_fi_section.sections;

  const mainPages = sections[0].section;
  const filteringSystems = sections[1].section;
  const personalLists = sections[2].section;
  const genresAndThemes = sections[3].section;
  const gameDetails = sections[4].section;
  const userProfiles = sections[5].section;
  const ratingCheckpoints = sections[6].section;

  // function PageNav() {
  //   // add active state based on position to the section
  //   return (
  //     <ul id="article-section-links">
  //       {sections.map(function (title) {
  //         return (
  //           <li id="nav-link-list-item">
  //             <a>{title}</a>
  //           </li>
  //         );
  //       })}
  //     </ul>
  //   );
  // }

  // function FiguresPlacement({ arr, cn }) {
  //   console.log(arr);
  //   // let len = arr.length
  //   let fraction = 0;
  //   console.log(fraction);
  //   return <div>{fraction}</div>;
  // }

  function ListItems({ arr, title }) {
    let list = arr;
    const cn = title + "-list";
    const licn = cn + "-item";
    return (
      <ul id="item-list-fn" className={`${cn}`}>
        {list.map(function (item) {
          return (
            <li id="list-item-fn" className={licn}>
              <span>{item}</span>
            </li>
          );
        })}
      </ul>
    );
  }

  return (
    <CaseStudyLayout frontmatter={frontmatter}>
      <CaseStudySection id={"overview"} cn={"case-study-section"}>
        <SectionHeader title="Overview" />
        <div className="case-study-info project-info">
          <div className="about-project centered-8">
            <TextHeader title="About GameShelf" subtitle="" />
            <p className="article-text project-description">
              {overview.project_description}
            </p>
          </div>
          <div className="grid-text-items">
            <div id="problem-statement" className="titled-text-block">
              <TextHeader title="Problem" subtitle="" />
              <p className="article-text">{overview.project_goal.problem}</p>
            </div>
            <div id="solution-statement" className="titled-text-block">
              <TextHeader title="Goals" subtitle="" />
              <p className="article-text">{overview.project_goal.solution}</p>
            </div>
          </div>
        </div>
        <div className="case-study-info detail-items-info">
          <div className="grid-text-items">
            <div id="role">
              <img
                id="role-img"
                className="text-decoration-image"
                src={RoleImage}
                alt={`Role - ${overview.role}`}
              />
            </div>
            <div id="tools">
              <h4 className="text-title">Tools</h4>
              <ListItems title="tools" arr={overview.tools} />
            </div>

            <div id="team">
              <img
                id="team-img"
                className="text-decoration-image"
                src={TeamImage}
                alt={`Team - ${overview.team}`}
              />
            </div>
          </div>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection
        id={"understanding"}
        cn={"case-study-section text-section"}
      >
        <SectionHeader title="Understanding" />

        <div className="user-research">
          <div className="text-title-container">
            <TextHeader
              title="User Research"
              subtitle="Identifying current pain points and what users want and need"
            />
            <div>
              <p className="article-text user-research-text">
                {project.preliminary_research.user_interviews}
              </p>
            </div>
          </div>
        </div>

        <div className="user-quotes">
          <ul className="user-quotes-text">
            {project.preliminary_research.user_quotes.map(function (item) {
              return (
                <li className="user-quote">
                  <q>{item}</q>
                </li>
              );
            })}
          </ul>
        </div>

        <div className="feedback">
          <TextHeader
            title="User Feedback"
            subtitle="Synthesized perceptions and perspectives"
          />
          {project.preliminary_research.user_feedback.map(function (item) {
            return <p className="article-text">{item}</p>;
          })}
        </div>

        <div className="competitive-analysis">
          <TextHeader
            title="Competitive Analysis"
            subtitle="Market understanding and product evalution"
          />
          <p className="article-text">{project.competitive_analysis.text}</p>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"design-process"} cn={"case-study-section"}>
        <SectionHeader title="Design Process" />

        <div id="sketches-design">
          <div className="sub-section-text centered-16">
            <TextHeader
              title={project.initial_design_concepts.sketching.title}
              subtitle={project.initial_design_concepts.sketching.subtitle}
            />

            <p className="article-text">
              {project.initial_design_concepts.sketching.text}
            </p>
          </div>

          <div className="images sketches">
            <figure className="large-figure sketch-1">
              <GatsbyImage
                fluid={
                  project.sketches[0].childImageSharp.gatsbyImageData.fluid
                }
                image={project.sketches[0].childImageSharp.gatsbyImageData}
                data-pin-no-hover="true"
                nopin="nopin"
              />
              <figcaption>
                A example of the crazy eights for creating paperwireframes which
                were then syntheized
              </figcaption>
            </figure>
            <figure className="small-figure sketch-2">
              <GatsbyImage
                fluid={
                  project.sketches[1].childImageSharp.gatsbyImageData.fluid
                }
                image={project.sketches[1].childImageSharp.gatsbyImageData}
                data-pin-no-hover="true"
                nopin="nopin"
              />
              <figcaption>
                Several iterations of cards happened using collaged elements
              </figcaption>
            </figure>
          </div>
        </div>

        <div id="wireframes-design">
          <div className="sub-section-text centered-16">
            <TextHeader
              title={project.initial_design_concepts.wireframes.title}
              subtitle={project.initial_design_concepts.wireframes.subtitle}
            />

            <p className="article-text">
              {project.initial_design_concepts.wireframes.text}
            </p>
          </div>
          <div className="images wireframes">
            {project.wireframes.map(function (image, i) {
              let len = project.wireframes.length;
              let num = i + 1;
              let captions = [
                "All Genres Page",
                "Discover Page",
                "Game List Page",
                "Genre List Page",
                "Home Page",
              ];
              return (
                <>
                  <figure className="wireframe">
                    <GatsbyImage
                      fluid={image.childImageSharp.gatsbyImageData.fluid}
                      image={image.childImageSharp.gatsbyImageData}
                      data-pin-no-hover="true"
                      nopin="nopin"
                    />
                    <figcaption>{captions[i]}</figcaption>
                  </figure>
                  {num !== len && <div className="one-col"></div>}
                </>
              );
            })}
          </div>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"usability-study"} cn={"case-study-section"}>
        <SectionHeader title={project.research_study_details.title} />

        <div className="study-details">
          <TextHeader title="Study Details" subtitle="" />
          <p className="article-text">{project.research_study_details.text}</p>
        </div>

        <div className="study-questions">
          <ul className="">
            {project.research_study_details.questions.map(function (question) {
              return (
                <li>
                  <p>{question}</p>
                </li>
              );
            })}
          </ul>
        </div>

        <div id="result-description">
          <TextHeader title="Study Results" subtitle="" />
          <p className="article-text">
            {project.research_study_details.result.text}
          </p>
        </div>
        <div className="results">
          <ul className="result-text">
            {project.research_study_details.result.research_results.map(
              function (result) {
                return (
                  <li>
                    <p>{result}</p>
                  </li>
                );
              }
            )}
          </ul>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"visual-design"} cn={"case-study-section"}>
        <SectionHeader title={project.visual_design.title} />

        <div id="color-and-fonts-container">
          <TextHeader
            title="Colors and Fonts"
            subtitle={project.visual_design.subtitle}
          />
          <p id="color-design" className="article-text">
            {project.visual_design.colors}
          </p>
          <p id="font-design" className="article-text">
            {project.visual_design.fonts}
          </p>

          <div className="images font-and-colors">
            <figure id="colors-and-fonts-image">
              <GatsbyImage
                fluid={
                  project.visual_design.images[0].childImageSharp
                    .gatsbyImageData.fluid
                }
                image={
                  project.visual_design.images[0].childImageSharp
                    .gatsbyImageData
                }
                data-pin-no-hover="true"
                nopin="nopin"
              />
              <figcaption>
                Colors and fonts were chosen for their modernity, clarity, and
                accessibility
              </figcaption>
            </figure>
          </div>
        </div>
        <div id="design-system-container">
          <TextHeader
            title="Design System"
            subtitle="Designing for consistent look and feel"
          />
          <p id="design-system" className="article-text">
            {project.visual_design.design_system}
          </p>
          <div className="images design-system">
            <figure id="components-image">
              <GatsbyImage
                fluid={
                  project.visual_design.images[1].childImageSharp
                    .gatsbyImageData.fluid
                }
                image={
                  project.visual_design.images[1].childImageSharp
                    .gatsbyImageData
                }
                data-pin-no-hover="true"
                nopin="nopin"
              />
              <figcaption>
                Repeated elements were made into components, a few of the most
                used are shown
              </figcaption>
            </figure>
          </div>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection
        id={"high-fidelity-interfaces"}
        cn={"case-study-section"}
      >
        <SectionHeader title={project.hi_fi_section.title} />

        <MainPagesSection data={mainPages} />

        <FilteringSystemsSection data={filteringSystems} />
        <GameDetailsSection data={gameDetails} />
        <GenresAndThemesSection data={genresAndThemes} />
        <PersonalListsSection data={personalLists} />
        <UserProfileSection data={userProfiles} />
        <RatingAndCheckpointsSection data={ratingCheckpoints} />
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"challenges"} cn={"case-study-section"}>
        <SectionHeader title={project.key_challenges_constraints.title} />

        <p id="main-text" className="article-text centered-8">
          {project.key_challenges_constraints.main_text}
        </p>

        <div id="challenges-list-container" className="centered-8">
          <ul>
            {project.key_challenges_constraints.constraints_and_challenges.map(
              function (item) {
                return (
                  <li>
                    <p>{item}</p>
                  </li>
                );
              }
            )}
          </ul>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"conclusion"} cn={"case-study-section"}>
        <SectionHeader title={conclusion.title} />
        <div id="final-thoughts" className="sub-section-text centered-8">
          <TextHeader title={conclusion.final_thoughts.title} subtitle="" />
          <p className="article-text">{conclusion.final_thoughts.text}</p>
        </div>
        <div id="next-steps-text" className="sub-section-text centered-8">
          <TextHeader title={conclusion.next_steps.title} subtitle="" />
          <p>{conclusion.next_steps.text}</p>
          <ul>
            {conclusion.next_steps.next_steps_array.map(function (item) {
              return (
                <li>
                  <p>{item}</p>
                </li>
              );
            })}
          </ul>
        </div>
      </CaseStudySection>
    </CaseStudyLayout>
  );
}

export const pageQuery = graphql`
  query MyQuery {
    allMarkdownRemark(filter: { frontmatter: { slug: { eq: "gameshelf" } } }) {
      edges {
        node {
          frontmatter {
            title
            sections
            tools
            banner_image {
              absolutePath
              childImageSharp {
                gatsbyImageData
                fluid {
                  srcSetWebp
                }
              }
            }
            project_goal {
              problem
              solution
            }
            team
            role
            time_frame
            project_description
            has_password

            conclusion {
              final_thoughts {
                text
                title
              }
              title
              next_steps {
                next_steps_array
                text
                title
              }
            }

            gameshelf {
              key_challenges_constraints {
                constraints_and_challenges
                main_text
                subtitle
                title
              }
              competitive_analysis {
                text
                title
              }
              initial_design_concepts {
                sketching {
                  subtitle
                  text
                  title
                }
                wireframes {
                  subtitle
                  text
                  title
                }
              }
              research_study_details {
                title
                subtitle
                text
                questions
                result {
                  text
                  research_results
                }
                final
              }
              preliminary_research {
                target_audience
                user_interviews
                user_feedback
                user_quotes
              }
              hi_fi_section {
                text
                title
                subtitle
                sections {
                  section {
                    title
                    text
                    subtitle
                    visuals {
                      images {
                        caption
                        img {
                          extension
                          childImageSharp {
                            gatsbyImageData
                            fluid {
                              srcSetWebp
                            }
                          }
                        }
                      }
                      gifs {
                        publicURL
                        extension
                      }
                    }
                  }
                }
              }
              visual_design {
                subtitle
                fonts
                colors
                design_system
                title
                images {
                  childImageSharp {
                    gatsbyImageData
                    fluid {
                      srcSetWebp
                    }
                  }
                }
              }
              sketches {
                childImageSharp {
                  gatsbyImageData
                  fluid {
                    srcSetWebp
                  }
                }
              }
              wireframes {
                childImageSharp {
                  gatsbyImageData
                  fluid {
                    srcSetWebp
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`;
