import React, { useState } from "react";
import CaseStudyHeader from "./caseStudyHeader";
import ButtonImage from "../../images/page-decoration/To Top Button.png";
import Layout from "../layout";

export default function CaseStudyLayout({ frontmatter, children }) {
  const header_data = {
    title: frontmatter.title,
    time_frame: frontmatter.time_frame,
    masthead_img: frontmatter.banner_image,
  };

  function backToTop() {
    window.scrollTo({ 
      top: 0,  
      behavior: 'smooth'
      /* you can also use 'auto' behaviour 
         in place of 'smooth' */
    }); 
  }

  return (
    <Layout name="case-study-page">
      <article id={`${frontmatter.title} case study`}>
        <CaseStudyHeader data={header_data} />
        {children}
        <footer className="case-study-footer">
          <div className="button-container">
            <button className="bottom-button" onClick={backToTop} title="Go to top">
              <img className="bottom-button-image" src={ButtonImage} />
            </button>
          </div>
        </footer>
      </article>
      {/* <nav className="two-col article-navigation">
            <div className="medium-empty-spacer-block"></div>
            <PageNav />
          </nav> */}
    </Layout>
  );
}
