import React from "react";
import TextHeader from "../../sectionTextHeader";
import { GatsbyImage } from "gatsby-plugin-image";

export function MainPagesSection({ data }) {
  const { title, subtitle, text, visuals } = data;

  const gifs = visuals[0].gifs;
  // const images = visuals[1].images

  const homePageGif = gifs[0].publicURL;
  const myGames = gifs[1].publicURL;
  const discoverPage = gifs[2].publicURL;

  return (
    <div id="main-pages-app" className="final-images-subsection">
      <div className="section-text">
        <TextHeader title={title} subtitle={subtitle} />
        <p className="article-text centered-16">{text}</p>
      </div>
      <div className="images main-pages">
        <img
          id="home-app"
          className="gif "
          src={homePageGif}
          data-pin-no-hover="true"
          nopin="nopin"
        />

        <img
          id="my-games-app"
          className="gif"
          src={myGames}
          data-pin-no-hover="true"
          nopin="nopin"
        />

        <img
          id="discover-app"
          className="gif"
          src={discoverPage}
          data-pin-no-hover="true"
          nopin="nopin"
        />
      </div>
    </div>
  );
}
