import React from "react";
import TextHeader from "../../sectionTextHeader";
import { GatsbyImage } from "gatsby-plugin-image";

export function PersonalListsSection({ data }) {
  const { title, subtitle, text, visuals } = data;
  const images = visuals[1].images;
  const gifs = visuals[0].gifs

  return (
    <div id="personal-lists-app" className="final-images-subsection">
        
      <div className="section-text">
        <TextHeader title={title} subtitle={subtitle} />
        <p className="article-text">
            {text}
        </p>
      </div>
      <div className="images personal-lists">
        <figure id="playing-game-list">
          <GatsbyImage
            fluid={images[0].img.childImageSharp.gatsbyImageData.fluid}
            image={images[0].img.childImageSharp.gatsbyImageData}
            data-pin-no-hover="true"
            nopin="nopin"
          />
          <figcaption>{images[0].caption}</figcaption>
        </figure>
        <img src={gifs[0].publicURL} className="gif"/>
        <figure id="add-to-games-overlay">
          <GatsbyImage
            fluid={images[1].img.childImageSharp.gatsbyImageData.fluid}
            image={images[1].img.childImageSharp.gatsbyImageData}
            data-pin-no-hover="true"
            nopin="nopin"
          />
          <figcaption>{images[1].caption}</figcaption>
        </figure>
      </div>
    </div>
  );
}
